<template>
    <div class="qdashboard">
        <div class="qdashboard__wrapper">
            <div class="qdashboard__wrapper__questions-container">
                
                <div class="qdashboard__wrapper__questions-container__item-container" v-for="(question,index) in getQuestions" :key="index" >
                    <!-- <div class="kldasj" v-if="question.answer == null">Es null</div> -->
                    <div @click="prueba(index+1)" class="qdashboard__wrapper__questions-container__item-container__item" :class="{active: question.answer == null}">
                        <p class="qdashboard__wrapper__questions-container__item-container__item__text">{{index+1}}</p>
                    </div>

                    <!-- <div v-else @click="prueba(index+1)" class="qdashboard__wrapper__questions-container__item-container__item">
                        <p class="qdashboard__wrapper__questions-container__item-container__item__text">{{index+1}}</p>
                    </div> -->
                </div>

            </div>
            <!-- <span class="uk-badge">1</span> -->
            <!-- {{question.answer}} -->
            <!-- {{question.question}} -->
        </div>
    </div>
    
</template>

<script>
export default {
    name: 'QuestionsDashboard',
    created(){
        if(this.$store.getters.getPaginate){

        }else{
            this.$router.push({ name: 'home'})
        }
    },
    data(){
        return {
            welcome: 'Bienvenido'
        }
    },
    computed: {
        getQuestions(){
            return this.$store.getters.getQuestions
        }
    },
    methods: {
        prueba(pregunta){
            this.$router.push({ name: 'surveyQuestion', params: { id: pregunta } })
        }

    }
    
}
</script>

<style lang="scss" scoped>
@import '../../scss/variables.scss';
.qdashboard{
    @include componentFather;
    background-color: $background;
    width: 100%;
    &__wrapper{
        // flex: 1 1 auto;
        // @media (min-width: $medium) {
        //     max-width: 1100px;
        //     margin: 0 auto;
        //     display: flex;
        //     flex-wrap: wrap;
        // }
        width: 100%;

        &__questions-container{
            width: 100%;
            display: grid;
            grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
            grid-template-rows: auto;
            grid-gap: 1em;
            &__item-container{
                display: flex;
                justify-content: center;    
                &__item{
                    text-align: center;
                    background: rgb(200, 200, 199);
                    width: 50px;
                    height: 50px;
                    border-radius: 50%;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    &__text{
                        margin: 0;
                        color: $background;
                    }
                    &.active{
                        background-color: skyblue;
                        p{
                            color: white;
                        }
                    }
                }

            }
        }

    }

}

</style>


